import React from "react";
import GenericPage from "../../Componentes/Generic_Page/GenericPage";

export default function Politicas() {
    return (
        <div>
            <GenericPage title="Politicas de Privacidade">
                <div>
                    <p>Na Sakura Card, a sua privacidade  uma prioridade. Esta Poltica de Privacidade explica como coletamos, usamos, armazenamos e protegemos as informaes dos nossos clientes ao utilizar nossos servios e plataformas, seja no site, redes sociais ou em nossas lojas fsicas.</p>
                    <h2>1. Coletamos informaes pessoais apenas quando necessrio para fornecer um servio eficiente e personalizado. Isso pode incluir:</h2>
                    <ul>
                        <li>Nome Completo</li>
                        <li>Endereo de Email</li>
                        <li>Endereo de entrega</li>
                        <li>Nmero de Telefone</li>
                        <li>Dados de pagamento (criptografados e processados de forma segura)</li>
                        <li>Histrico de Compras</li>
                    </ul>
                    <p>Essas informaes so obtidas quando voc realiza um pedido, se cadastra em nosso site, participa de promoes ou entra em contato conosco.</p>
                    <h2>2. Uso das Informaes</h2>
                    <p>As informaes coletadas so utilizadas para:</p>
                    <ul>
                        <li>Processamento de pedidos e entregas</li>
                        <li>Atendimento ao cliente</li>
                        <li>Envio de ofertas, promoes e novidades (com consentimento prvio)</li>
                        <li>Melhorias na experincia do usurio e nos servios oferecidos</li>
                        <li>Cumprimento de obrigaes legais e regulatrias</li>
                    </ul>
                    <h2>3. Compartilhamento de Dados</h2>
                    <p>A Sakura Card no vende nem aluga seus dados pessoais para terceiros. Podemos compartilhar suas informaes apenas com:</p>
                    <ul>
                        <li>Fornecedores de servios de pagamento e entrega, sempre com a devida proteo contratual</li>
                        <li>Autoridades legais, em caso de obrigaes jurdicas</li>
                    </ul>
                    <h2>Segurana</h2>
                    <p>Adotamos medidas tcnicas e organizacionais para proteger seus dados contra acesso no autorizado, perda, roubo ou uso indevido. Nosso site utiliza criptografia SSL para transaes e seguimos prticas de segurana reconhecidas no setor.</p>
                    <h2>Cookies e Tecnologias Semelhantes</h2>
                    <p>Utilizamos cookies para melhorar sua experincia no site, entender o comportamento do usurio e oferecer promoes personalizadas. Voc pode gerenciar suas preferncias de cookies diretamente no navegador.</p>
                    <h2>6. Direitos do Usurio</h2>
                    <p>Voc tem o direito de:</p>
                    <ul>
                        <li>Acessar seus dados pessoais</li>
                        <li>Solicitar correo ou excluso de informaes</li>
                        <li>Retirar o consentimento para comunicaes de marketing</li>
                        <li>Solicitar a portabilidade dos dados, se aplicvel</li>
                    </ul>
                    <h2>7. Alteraes nesta Poltica</h2>
                    <p> Sakura Card pode atualizar esta Poltica de Privacidade periodicamente. Recomendamos que voc a consulte regularmente. Mudanas significativas sero informadas por e-mail ou em nosso site.</p>
                </div>
            </GenericPage>
        </div>
    )
}